
body {
  margin: 0 !important;
  padding: 0 !important;
  overflow-y:auto;
  width:100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


h1, h2, h3 {
  margin: 0px;
}


:root {
  --bar-height: 45px;
  --Main-color: #1463F3; 
  --Secondary-color: rgb(0, 0, 0);
  --Third-color:rgb(67, 210, 253);
  --Main-text-color: white;
}

input[type=text] {
  transition-duration: 0.1s;
  box-sizing: border-box;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 2px solid black;
}
input[type=text]:focus {
  transition-duration: 0.1s;
  color: black;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 4px solid rgb(255, 255, 255);
}

input[type=submit], button {
  background-color: rgb(0, 0, 0);
  background-color: var(--Secondary-color);
  padding: 5px;
  border: none;
  color: white;
}

input[type=submit]:hover, button:hover {
  transition-duration: 0.1s;
  background-color: rgb(167, 193, 226);
}

input[type=submit]:active, button:active {
  transition-duration: 0.1s;
  background-color: rgb(114, 132, 156);
}


h2, h1 {
  margin: 0;
}

h2 {
  padding-left: 10px;
  padding-top: 10px;
}

#greeter {
  background-color: var(--Main-color);
  color: white;
  text-align: center;
  width: 100%;
  height: 300px;
  line-height: 300px;
}

.filter {
  background-color: var(--Secondary-color);
  width: 100%;
  height: 60px;
  font-size: 30px;
  font-weight: 800;
  color: var(--Main-text-color);
}

.CardFlow {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  height: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  grid-gap: 30px;
  gap: 30px;
}
.Navbar {
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: var(--bar-height);
  background-color: rgb(32, 32, 32);
  top: 0;
  z-index: 1;
}


.NavbarRight > Link, a {
  text-decoration: none;
  color: var(--text-color);
}

.NavbarLeft, .NavbarRight {
  display: inline-flex;
}

.NavbarLeft > a >  div, .NavbarRight > a > div {
  padding-left: 7px;
  padding-right: 7px;
  height: inherit;
  color: white;
  line-height: var(--bar-height);
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.NavbarLeft > a > div:hover, .NavbarRight > a >  div:hover {
  transition-duration: 0.1s;
  background-color: rgb(84, 84, 84);
}

.NavbarLeft > a > div:active, .NavbarRight > a >  div:active {
  transition-duration: 0.1s;
  background-color: rgb(44, 44, 44);
}

#LoginButton {
  text-decoration: none;
  height: calc(var(--bar-height) - 7px);
  line-height: calc(var(--bar-height) - 7px);
  border-radius: 7%;
  background-color: grey;
  margin-top:   3.5px;
  margin-right: 7px;
  padding-left: 10px;
  padding-right:10px;
}

#LoginButton:hover {
  background-color: rgb(84, 84, 84);
}

#LoginButton:active {
  background-color: rgb(44, 44, 44);
}
.Footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(var(--bar-height)* 1.5);
  background-color: var(--Secondary-color);
  color: var(--Main-text-color);
  font-size: 12px;
}

.Footer > p {
  line-height: 0;
  font-style: italic;
}
.CardWrapper {
  transition-duration: 300ms;
  width:  400px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: left;
}

.CardWrapper:hover {
  transition-duration: 300ms;
  -webkit-filter: brightness(170%);
          filter: brightness(170%);
  scale: 90%;
}

.CardWrapper:active {
  transition-duration: 300ms;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}




.CardTitle{
  background-color: var(--Main-color);
  color: var(--Main-text-color);
  flex: 4 1;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.CardFooter {
  flex: 1 1;
  background-color: black;
  color: var(--Main-text-color);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.RootLayout {
  height:100vh;
  overflow-y: auto;
  color: black;
}

.ScrollableWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 0px;
  gap: 0px;
  width: 100%;
  height: calc(100% - var(--bar-height));
}

.LoginWrapper {
  background-color: rgb(255, 255, 255);
  flex: 2 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.LoginBox {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  padding: 25px;
  border-radius: 30px;
  width: 30%;
  background-color: var(--Main-color);
}

.LoginBox > label {
  font-weight: 900;
  font-size: 60px;
  font-style: italic;
}

.LoginBox > button {
  width: 15%;
}

.LoginFields {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-gap: 30px;
  gap: 30px;
}

.LoginRow > label {
  font-weight: 900;
  font-size: 19px;
  font-style: italic;
}


.LoginActions {
  display: flex;
  grid-gap:10px;
  gap:10px;
}



#Graph {
  margin: 0 auto;
}



.Main {
  padding-top: 30px;
  height:calc(100vh - var(--bar-height) );
  display: flex;


  flex-direction: column;
  align-items: center;
  vertical-align:middle;
}

h2, h1 {
  margin: 0;
}

h2 {
  padding-left: 10px;
  padding-top: 10px;
}



.formDivider {
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
}

.postForm {
  display: flex;
  background-color: var(--Main-color);
  flex-direction: column;
  width: 90vw;
  padding: 30px;
  margin-bottom: 30px;
}

.Actions {
  display: flex;
  align-items: center;
  justify-content:center;
  padding: 20px;
}

.Actions > input {
  transition-duration: 0.1ms;
  height: 30px;
  border: none;
  background-color: rgb(184, 215, 255);
  color: black;
  font-weight: 900;
}

.Actions > input:hover {
  transition-duration: 0.1ms;
  background-color: rgb(167, 193, 226);
}

.Actions > input:active {
  transition-duration: 0.1ms;
  background-color: rgb(114, 132, 156);
}

.PostRow {
  display: flex;
  width: 90%;
  grid-gap: 10px;
  gap: 10px;
}


.PostFields {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-gap: 30px;
  gap: 30px;
  padding: 10px;
}


.PostRow > label {
  font-weight: 900;
  font-size: 19px;
  font-style: italic;
}

.PostRow > input[type=text] {
  transition-duration: 0.1s;
  box-sizing: border-box;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 2px solid black;
}

.PostRow > input[type=text]:focus {
  transition-duration: 0.1s;
  color: black;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 4px solid rgb(255, 255, 255);
}


.PostRow > input[type=number] {
  transition-duration: 0.1s;
  box-sizing: border-box;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 2px solid black;
}

.PostRow > input[type=number]:focus {
  transition-duration: 0.1s;
  color: black;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 4px solid rgb(255, 255, 255);
}

.LoadSpinner {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.9);
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.LoadSpinner h4 {
  color: white;
  font-style: italic;
  font-size: 20px;
}

.LoadSpinner img{
  height: auto;
  max-height: 15%;
  width: auto;
  -webkit-animation: rotation 2s infinite linear;
          animation: rotation 2s infinite linear;
  max-width: 15%;
}


@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}


@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
